@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%; }

a {
  color: #f9fbfd;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f9fbfd;
  -webkit-text-fill-color: #011c40; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.is-show {
  display: block; }

.is-hidden {
  display: none; }

.button {
  display: inline-flex;
  margin: 10px auto;
  padding: 10px 20px;
  width: auto;
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #f9fbfd;
  border-radius: 7px;
  background-color: #febab1;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
  user-select: none;
  -webkit-tap-highlight-color: transparent; }
  .button:disabled {
    opacity: 0.5;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .button:hover, .button:focus {
      opacity: 0.8; } }

.banner {
  padding: 40px 0;
  box-shadow: 0 0 5px 1px rgba(34, 60, 80, 0.2);
  width: 80%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  text-align: left; }
  @media (max-width: 767px) {
    .banner {
      padding: 20px 0; } }

.banner__container {
  padding: 0 40px; }
  @media (max-width: 767px) {
    .banner__container {
      padding: 0 20px; } }

.banner h1 {
  margin: 0;
  font-size: 39px;
  line-height: 45px;
  color: #febab1;
  font-weight: 700; }
  @media (max-width: 767px) {
    .banner h1 {
      font-size: 30px;
      line-height: 36px; } }

.banner__meta {
  margin-bottom: 25px; }

.banner__meta time {
  font-size: 15px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.3); }

.banner p {
  margin: 0;
  font-size: 16px;
  line-height: 25px;
  margin-bottom: 30px; }
  .banner p:last-child {
    margin-bottom: 0; }
  @media (max-width: 767px) {
    .banner p {
      font-size: 15px;
      line-height: 21px; } }

.banner__list {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 25px;
  margin-bottom: 30px;
  padding-left: 50px; }
  @media (max-width: 767px) {
    .banner__list {
      font-size: 15px;
      line-height: 21px;
      padding-left: 30px; } }

.banner__list li {
  margin-bottom: 10px; }

.banner__list--features {
  list-style: none;
  padding-left: 30px; }
  @media (max-width: 767px) {
    .banner__list--features {
      padding-left: 12px; } }

.banner__list--features li {
  position: relative;
  padding-left: 20px; }

.banner__list--features li::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  background-image: url("../img/svg/icon-tick.svg");
  background-repeat: no-repeat;
  width: 15px;
  height: 15px; }

.banner__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 30px; }
  @media (max-width: 767px) {
    .banner__wrapper {
      flex-direction: column; } }

.banner__image-wrapper {
  width: 130px;
  height: 100px;
  margin-right: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f2efe9;
  border-radius: 7px; }
  @media (max-width: 767px) {
    .banner__image-wrapper {
      margin-bottom: 20px;
      margin-right: 0;
      width: 150px;
      height: 120px; } }

.banner__image-wrapper img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 7px; }

.banner__button {
  margin-right: 30px;
  margin-left: 0; }
  @media (max-width: 767px) {
    .banner__button {
      margin-right: 0;
      margin-bottom: 20px; } }

.banner span {
  color: rgba(1, 28, 64, 0.3);
  text-transform: uppercase; }

.banner p span {
  color: #ff0000;
  text-transform: none; }

.timer__item {
  position: relative;
  min-width: 100px;
  margin-left: 10px;
  margin-right: 10px;
  padding-right: 70px;
  text-align: center;
  white-space: nowrap; }
  .timer__item:last-child {
    margin-left: 0;
    padding-right: 80px; }

.timer__item::before {
  content: attr(data-title);
  display: block;
  position: absolute;
  left: 25px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px; }

